











import Vue from "vue";
import RenderCustomComponent from "./RenderCustomComponent.vue";

export default Vue.extend({
  name: "iInput",
  components: {
    RenderCustomComponent
  },
  props: {
    value: {
      default: null
    },
    renderConfig: {
      type: Object,
      default: null
    },
    scope: {
      default: null
    }
  },
  data() {
    return {
      val: this.value
    };
  },
  watch: {
    value(newVal, oldVal) {
      this.val = newVal;
    }
  },
  methods: {
    handleInput(val: any) {
      this.$emit("input", val);
    }
  }
});
