







import Vue from "vue";
import { isExternal } from "@/utils/validate";

export default Vue.extend({
  props: {
    to: {
      type: String,
      required: true
    }
  },
  methods: {
    linkProps(url: string) {
      if (isExternal(url)) {
        return {
          is: "a",
          href: url,
          target: "_blank",
          rel: "noopener"
        };
      }
      return {
        is: "router-link",
        to: url
      };
    }
  }
});
